:root {
    --background-color: #181d20;
    --chart-color: #131b1d;
    --artist-text-color: #585858;
    --icon-color: #FACD66;
}
* {
    padding: 0;
    margin: 0;
    text-decoration: none;
    list-style: none;
    box-sizing: border-box;
}
body {
    font-family: Poppins, sans-serif;
    font-size:1rem;
    padding:0px;
    margin:0px;
    background-color: #181d20;
    background-color: var(--background-color);
    color:#fff;
}
h1 {
    font-size:1.4em;
}
h2 {
    font-size:1em;
    color:#92979a;
}
h3 {
    font-size:1em;
    color:#92979a;
}
p {
    font-size:0.7em;
    color:#92979a;
}
.app {
    width:100%;
}
.flex {
    display:flex;
    margin-top:2em;
}
.bottom {
    display:block;
    margin:5em 0;
}
.top {
    width:100%;
    padding:0;
    margin:0;
    display:flex;
    margin:2em 0;
}
.contain {
    padding:1em 2em 2.3em 5.3em;
    width:100%;
}

.top-bar {
    width:100%;
    display:flex;
    align-items:center;
    position:fixed;
    top:0;
    padding:0.5em 1.5em;
    margin-bottom:3rem;
    background-color:  var(--background-color);

}
.search-bar {
    display:flex;
    align-items:center;
    border-radius:100px;
    padding:0 1em;
    margin:0 1.8em;
}
input {
    margin:0 0.5em;
    padding:0.3em 0.5em;
    border:none;
    outline:none;
    border-radius:100px;
    background-color: rgba(24,29,32,.2);
    caret-color:#fff;
    color:#fff;
    width:100%;
}
input:active, input:hover, input:focus {
    background-color: rgba(24,29,32,.2);
}
.menubar div {
    margin:0.3em 0;
    margin-right:1em;
    background:#fff;
    height:3px;
    width:1.5em;
    border-radius:100px;
}
.menu-bar {
    display:block;
    background-color: rgba(0,0,0,.2);
    text-align:center;
    margin:1.5em 1em;
    padding:0.3em 0.2em;
    border-radius:100px;
}
.menu-bar div{
    margin:1.5em 0;
    padding:0;
}
.cold{
    fill-opacity:0.25;
}
.cold:hover {
    fill-opacity:1;
    fill: var(--icon-color);
}
.active {
    fill: var(--icon-color);
}
.side-bar {
    display:block;
    position:fixed;
    height:100vh;
    padding-top:1.4em;
}

@media only screen and (max-width: 740px) {
    /* sidebar */
    .side-bar {
        display:none;
    }
    /* Top body */
    /* current play */
    .contain {
        padding:1em;
        width:100%;
    }
    .top {
        display:block;
        width:100%;
        padding:0;
    }
    .artistimg, .artistimg img{
        display:none;
    }
}
.current_play {
    background-image:url(/static/media/Vector.84bd4db1.svg), url(data:image/png;base64,);
    background-repeat:no-repeat;
    background-position:center;
    background-size:cover;
    border-radius:40px;
    margin:0;
    padding:0;
    margin-right:1.2em;
    width:60%;
    display:flex;
    justify-content:space-between;
}
.play_details {
    display:flex;
    flex-direction:column;
    justify-content:space-between;
    padding:2rem 0 2em 3em;
    height:100%;
}
.current_play p {
    color:#fff;
}
.frame {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    grid-gap: 0.7em;
    gap: 0.7em;
}
.artistimg {
    display:flex;
    align-items:flex-end;
}
@media only screen and (max-width: 740px) {
    .current_play {
        width:100%;
        height:400px;
        margin-bottom:2em;
    }
    .play_details {
        padding:2rem 0 2em 1em;
    }
}
.fl {
    display:flex;
    justify-content:space-between;
    background-color: var(--chart-color);
    margin:1em 0;
    border-radius:20px;
    align-items:center;
    padding:0.5em 1em;
    width:100%;
}
.fl h2, .fl p {
    margin:0;
    padding:0;
}
.artist {
    color: var(--artist-text-color);
}
.chart_details {
    margin:0 1em;
    width:100%;
}
.chart {
    width:40%;
}
.divide {
    display:flex;
    align-items:center;
}
.fl svg {
    margin:0 0.4em;
}
.fl svg:hover, .fl svg:active, .fl svg:focus {
    fill: var(--icon-color);
    fill-opacity:1;
}
.like {
    display:flex;
    align-items:center;
    justify-content:center;
    border-radius:50%;
    outline:2px solid #222;
    width:30px;
    height:30px;
}
@media only screen and (max-width: 740px) {
    .chart {
        width:100%;
    }
}
.song_section::-webkit-scrollbar {
    display: none;
} 
.song_section {
    display:flex;
    overflow: hidden;
    overflow-x: scroll;
    white-space: nowrap;
    -ms-overflow-style: none;
    scrollbar-width: none;
    padding:1em 0;
    margin:1em 0;
}
.song_section h3 {
    margin-top:0.4em;
}
.song_block {
    margin-right:2em;
}
.control_bar {
    width:100%;
    display:flex;
    justify-content:space-between;
    align-items:center;
    margin:0;
    padding:0 5em;
    position:fixed;
    bottom:0;
    background-color: rgba(0,0,0,.7);
    -webkit-backdrop-filter: blur(10px);
            backdrop-filter: blur(10px);
}
.song_play {
    display:flex;
    padding:0;
    margin:0;
}
.song_play_details p {
    margin:0;
    padding:0;
}
.song_play_details {
    margin-left:1em;
    display:flex;
    flex-direction:column;
    justify-content:center;
}
.song_play_details h3 {
    color:#fff;
}
.vol-control {
    display:flex;
    grid-gap:0.5em;
    gap:0.5em;
    padding-left:15px;
}
.control {
    display:flex;
    align-items:center;
    grid-gap:0.5em;
    gap:0.5em;
}
.control div{
    margin:0 0.5em;
}
.play_btn {
    display:none;
}
/* slider */
.slider {
    width:100%;
}
input[type=range] {
    -webkit-appearance: none;
    width:100%;
    height:4px;
    padding:0;
    margin:0;
    background-image: linear-gradient(#FACD66, #FACD66);
    background-size: 0%;
    background-repeat: no-repeat;
}
input[type=range]:focus {
    outline: none;
}
/* slider thumb */
input[type=range]::-webkit-slider-thumb {
    -webkit-appearance: none;
    appearance:none;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 9px;
    height:9px;
    aspect-ratio: 1/1;
    border-radius: 50%;
    background: var(--icon-color);
    cursor:pointer;
    outline: 2px solid #fff;
    outline-offset: 3px;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.92);
    -webkit-transition: background .3s ease-in-out;
    transition: background .3s ease-in-out;
    margin-top:2px;
}
input[type="range"]::-moz-range-thumb {
    -webkit-appearance: none;
    height: 9px;
    width: 9px;
    border-radius: 50%;
    background: #fff;
    cursor: pointer;
    outline:none;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.92);
    -moz-transition: background .3s ease-in-out;
    transition: background .3s ease-in-out;
    margin-top: 10px;
}
input[type="range"]::-ms-thumb {
    -webkit-appearance: none;
    height: 9px;
    width: 9px;
    border-radius: 50%;
    background: #fff;
    cursor: pointer;
    outline: none;
    box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.92);
    -ms-transition: background .3s ease-in-out;
    transition: background .3s ease-in-out;
    margin-top: 10px;
} 
/* input track*/
input[type=range]::-webkit-slider-runnable-track {
    background:rgba(255,255,255,0.15);
    height:4px;
    cursor:pointer;
}

input[type=range]::-moz-range-track {
    background:rgba(255,255,255,0.15);
    height:4px;
    cursor:pointer;
}

input[type="range"]::-ms-track {
    background:rgba(255,255,255,0.15);
    height:4px;
    cursor:pointer;
}
.fl-small {
    width:20%;
}
.fl-big {
    width:60%;
    display:flex;
    flex-direction:column;
    justify-content:center;
    align-items:center;
    padding:1em 0;
}
@media only screen and (max-width: 740px) {
    .control_bar {
        width:100%;
        margin:0;
        padding:0 0.5em;
        justify-content:space-between;
    }  
    .slider, .control, .vol-control, .vol-bar {
        display:none;
    }
    .play_btn {
        display:flex;
    }
    .cont {
        width:100%;
        display:flex;
        justify-content:right;
    }
}
